import React from 'react';
import { createUseStyles, useTheme } from 'react-jss';
import { useHistory } from 'react-router-dom';
import SLUGS from 'resources/slugs';
import {
    IconAgents,
    IconArticles,
    IconContacts,
    IconSubscription,
    IconTickets
} from 'assets/icons';
import { convertSlugToUrl } from 'resources/utilities';
import LogoComponent from './LogoComponent';
import Menu from './MenuComponent';
import MenuItem from './MenuItemComponent';

const useStyles = createUseStyles({
    separator: {
        borderTop: ({ theme }) => `1px solid ${theme.color.lightGrayishBlue}`,
        marginTop: 16,
        marginBottom: 16,
        opacity: 0.06
    }
});

function SidebarComponent() {
    const { push } = useHistory();
    const theme = useTheme();
    const classes = useStyles({ theme });
    const isMobile = window.innerWidth <= 1080;

   

    function onClick(slug, parameters = {}) {
        push(convertSlugToUrl(slug, parameters));
    }

    return (
        <Menu isMobile={isMobile}>
            <div style={{ paddingTop: 30, paddingBottom: 30 }}>
                <LogoComponent />
            </div>
            <MenuItem
                id={SLUGS.TrafficAnalysis}
                title='TrafficAnalysis'
                icon={IconSubscription}
                onClick={() => onClick(SLUGS.TrafficAnalysis)}
            />
            
               
            <MenuItem
                id={SLUGS.SocialStudio}
                title='Social Studio'
                icon={IconTickets}
                onClick={() => onClick(SLUGS.SocialStudio)}
            />
            
           
            <MenuItem
                id={SLUGS.NewsLetter}
                title='News Letter'
                icon={IconContacts}
                onClick={() => onClick(SLUGS.NewsLetter)}
            />
            <MenuItem
                id={SLUGS.Leads}
                title='Leads'
                icon={IconAgents}
                onClick={() => onClick(SLUGS.Leads)}
            />
            <MenuItem
                id={SLUGS.LandingPage}
                title='Landing Page'
                icon={IconArticles}
                onClick={() => onClick(SLUGS.LandingPage)}
            />
            <MenuItem
                id={SLUGS.Cmr}
                title='CMR'
                icon={IconSubscription}
                onClick={() => onClick(SLUGS.Cmr)}
            />
            <div className={classes.separator}></div>
           

           
        </Menu>
    );
}

export default SidebarComponent;
